import Radium from 'radium';

export default function Book(props) {
  return (
    <div style={props.style}>
      <a href={props.url}>
        <img style={styles.images} src={props.imageSource} alt={props.altText} />
      </a>
    </div>
  );
}

var styles = {
  images: {
    boxShadow: '-5px 5px 10px #888888',
    maxWidth: '300px',
  }
}
Book = Radium(Book);